import React, { useState } from "react";
import PropTypes from "prop-types";
import bridge from "@vkontakte/vk-bridge";

import "./Home.css";
import {
  Panel,
  PanelHeader,
  Group,
  Cell,
  Div,
  Avatar,
  View,
  ScreenSpinner,
  Title,
} from "@vkontakte/vkui";
import AddNotesView from "./components/AddNotesView";

function Home({
  id,
  fetchedUser,
  setActivePanel,
  data,
  db,
  setNotes,
  findDocumentById,
}) {
  //const [todos, setTodos] = useState([]);
  const [loading, setLoading] = useState(<ScreenSpinner size="large" />);
  const [notes1, setNotes1] = useState("");
  const [notes2, setNotes2] = useState("");
  const [notes3, setNotes3] = useState("");
  const [notes4, setNotes4] = useState("");
  let freeSlots = "";
  data.lastTitle = "";

  function setTimer(value, f) {
    if (value) {
      f();
    } else {
      setTimeout(() => {
        setTimer(value, f);
      }, 100);
    }
  }

  function waitForDocId() {
    if (typeof data.docId !== "undefined") {
      getNotes();
    } else {
      setTimeout(() => {
        waitForDocId();
      }, 100);
    }
  }

  let user = null;
  function waitForUser() {
    if (user) {
      data.userId = user.id.toString;
      findDocumentById(user.id.toString());
      waitForDocId();
    } else {
      setTimeout(() => {
        waitForUser();
      }, 100);
    }
  }

  async function fetchData() {
    user = await bridge.send("VKWebAppGetUserInfo");
    waitForUser();
  }
  fetchData();

  const NotesPreview = React.lazy(
    () =>
      new Promise((resolve) => {
        setTimer(notes1, resolve(import("./components/NotesPreview")));
      })
  );

  function updateNotesData() {
    data.notes1 = notes1;
    data.notes2 = notes2;
    data.notes3 = notes3;
    data.notes4 = notes4;
  }

  function getNotes() {
    db.collection("notes")
      .doc(data.docId)
      .get()
      .then(function (doc) {
        if (doc.exists) {
          setLoading(null);
          if (doc.data().notes1) {
            setNotes1(doc.data().notes1);
          } else {
            setNotes1("");
            freeSlots += "1";
          }
          if (doc.data().notes2) {
            setNotes2(doc.data().notes2);
          } else {
            setNotes2("");
            freeSlots += "2";
          }
          if (doc.data().notes3) {
            setNotes3(doc.data().notes3);
          } else {
            setNotes3("");
            freeSlots += "3";
          }
          if (doc.data().notes4) {
            setNotes4(doc.data().notes4);
          } else {
            setNotes4("");
            freeSlots += "4";
          }
          updateNotesData();
        }
      });
  }

  function addNewNotes() {
    var notesId = freeSlots[0];
    data.notesId = notesId;
    if (notesId == "1") {
      data.notes1 = "Новая заметка";
      setNotes1("Новая заметка");
    } else if (notesId == "2") {
      data.notes2 = "Новая заметка";
      setNotes2("Новая заметка");
    } else if (notesId == "3") {
      data.notes3 = "Новая заметка";
      setNotes3("Новая заметка");
    } else {
      data.notes4 = "Новая заметка";
      setNotes4("Новая заметка");
    }
    setNotes(
      data.docId,
      data.userId,
      data.notes1,
      data.notes2,
      data.notes3,
      data.notes4
    );
    setActivePanel("persik");
  }

  function onNotesClick({ notesId }) {
    data.notesId = notesId;
    let notesById = "";
    if (notesId == "1") {
      notesById = notes1;
    } else if (notesId == "2") {
      notesById = notes2;
    } else if (notesId == "3") {
      notesById = notes3;
    } else {
      notesById = notes4;
    }
    setActivePanel("persik");
  }

  return (
    <View activePanel={id} popout={loading}>
      <Panel id={id}>
        <PanelHeader>Список задач</PanelHeader>
        {fetchedUser && (
          <Group title="User Data Fetched with VK Bridge">
            <Cell
              before={
                fetchedUser.photo_200 ? (
                  <Avatar src={fetchedUser.photo_200} />
                ) : null
              }
              description={
                fetchedUser.city && fetchedUser.city.title
                  ? fetchedUser.city.title
                  : ""
              }
            >
              {`${fetchedUser.first_name} ${fetchedUser.last_name}`}
            </Cell>
          </Group>
        )}

        {/* Notes */}
        <Div className="wrapper">
          <React.Suspense fallback={<div></div>}>
            {notes1.split("₪")[0] && (
              <NotesPreview
                notesId={1}
                title={notes1.split("₪")[0]}
                onClick={onNotesClick}
              />
            )}
            {notes2.split("₪")[0] && (
              <NotesPreview
                notesId={2}
                title={notes2.split("₪")[0]}
                onClick={onNotesClick}
              />
            )}
            {notes3.split("₪")[0] && (
              <NotesPreview
                notesId={3}
                title={notes3.split("₪")[0]}
                onClick={onNotesClick}
              />
            )}
            {notes4.split("₪")[0] && (
              <NotesPreview
                notesId={4}
                title={notes4.split("₪")[0]}
                onClick={onNotesClick}
              />
            )}
          </React.Suspense>
          {!notes1.split("₪")[0] ||
          !notes2.split("₪")[0] ||
          !notes3.split("₪")[0] ||
          !notes4.split("₪")[0] ? (
            <AddNotesView onCreate={addNewNotes} />
          ) : (
            <div className="nofree">
              <Title weight="semibold">
                В целях экономии места доступно 4 слота :(
              </Title>
            </div>
          )}
        </Div>
      </Panel>
    </View>
  );
}

Home.propTypes = {
  id: PropTypes.string.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
};

export default Home;
